                                                          background: url(../img/arrow-white.png) no-repeat;
                                                          width: 100px;
                                                          height: 80px;
                                                          right: 42%;
                                                          bottom: -29%; }

                                                          .calling-me {
                                                            text-align: right;
                                                            margin-top: 70px;
                                                            padding-left: 0;
                                                            margin-left: 0; }

                                                            a.roket-link {
                                                              display: block;
                                                              width: 55px;
                                                              height: 55px;
                                                              background: url(../img/roket.png) no-repeat;
                                                              position: absolute;
                                                              left: 50%;
                                                              bottom: 10px;
                                                              transition: all 0.5s ease 0.1s; }

                                                              a.roket-link:hover {
                                                                transform: rotate(180deg); }

                                                                #my-menu {
                                                                  display: none;
                                                                }
