}

.top-menu ul li:before {
  content: '';
  position: absolute;
  height: 50%;
  top: 25%;
  right: 0;
  border-right: 1px solid #ffffff;
}

.top-menu ul li:last-child:before {
  border-right: none;
}

.top-menu ul li a {
  text-align: center;
  font-size: 0.875rem;
  line-height: 1.2;
  position: relative;
  display: block;
  padding: 5px 10px;
