  text-transform: uppercase;
  color: #ffffff;
  transition: all 1.5s ease;
}

.top-menu ul li a:hover:before {
  position: absolute;
  content: '';
  width: 80%;
  left: 10%;
  bottom: 0;
  border-bottom: 1px dashed #F89430;
}

